import React, { useState } from 'react';
import './Login.css';
import emailjs from '@emailjs/browser';
import  secureLocalStorage  from  "react-secure-storage";

function Login() {

    let [code, setCode] = useState("");

    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

    function generateString() {
        let result = '';
        const charactersLength = characters.length;
        for ( let i = 0; i < 16; i++ ) {
            result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }

        return result;
    }

    function sendUniqueCode() {
        let email = document.querySelector(".email").value;
        if(email === "harshitbhargava042001@gmail.com" || email === "shivendra.shivendra.mall@gmail.com" || email === "laundrybuddyforyou@gmail.com") {
            document.querySelector(".password").style.display = "block";
            document.querySelector(".email-button").style.display = "none";
            document.querySelector(".password-button").style.display = "block";

            let randomString = generateString();

            setCode(randomString);

            emailjs.send("service_mjttko9", "template_wlq0g4x", {
                code: randomString,
                email: email
            }, "gkM2SdSnL3a-1yUti");
        }
    }

    function checkPassword() {
        let passcode = document.querySelector(".password").value;
        if(passcode === code) {
            secureLocalStorage.setItem("login", true);
            window.location = "/";
        }
    }

    let loginStatus = secureLocalStorage.getItem("login");
    if(loginStatus === true) {
        window.location = "/";
    }

    return (
        <div className='login-body'>
            <div className="login">
                <h1>Login</h1>
                <form>
                <input type="email" className='email' placeholder="Email ID" required />
                <input type="password" className='password' placeholder="Password" required />
                <button onClick={sendUniqueCode} type="submit" className="btn btn-primary btn-block btn-large email-button">Send Code</button>
                <button onClick={checkPassword} type="submit" className="btn btn-primary btn-block btn-large password-button">Let me in.</button>
                </form>
            </div>
        </div>
    )
}

export default Login