import React, {useEffect, useState} from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import './Header.css';

function Header() {

    const [expanded, setExpanded] = useState(false);

    const selectClick = () => {
        let left_select = document.querySelectorAll(".nav-custom-link");
        
        for(let j = 0; j<left_select.length; j++) {
            left_select[j].addEventListener('click', () => {
                setExpanded(false)

                if(j === 0) {
                    document.querySelector(".billing-container").style.display = "flex";
                    document.querySelector(".existing-order-container").style.display = "none";
                } else if (j === 1) {
                    document.querySelector(".billing-container").style.display = "none";
                    document.querySelector(".existing-order-container").style.display = "flex";
                }
            })
        }
    }

    useEffect(() => {
        selectClick();
    }, [])

    return (
        <Navbar expanded={expanded} expand="lg" bg="dark" variant="dark" id='navbar'>
        <Container>
            <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={() => setExpanded(true)} />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="me-auto justify-content-end">
                    <Navbar.Text className='nav-link nav-custom-link' onClick={selectClick}>New Order</Navbar.Text>
                    <Navbar.Text className='nav-link nav-custom-link' onClick={selectClick}>Existing Orders</Navbar.Text>
                </Nav>
            </Navbar.Collapse>
        </Container>
        </Navbar>
    )
}

export default Header