import React, { useEffect } from 'react';
import './LeftComponent.css';

function LeftComponent() {

    const selectClick = () => {
        let left_select = document.querySelectorAll(".left-select");
        
        for(let j = 0; j<left_select.length; j++) {
            left_select[j].addEventListener('click', () => {
                for(let i = 0; i<left_select.length; i++) {
                    left_select[i].classList.remove("active");
                }
                left_select[j].classList.add("active");

                if(j === 0) {
                    document.querySelector(".billing-container").style.display = "flex";
                    document.querySelector(".existing-order-container").style.display = "none";
                    document.querySelector(".order-analytics-container").style.display = "none";
                } else if (j === 1) {
                    document.querySelector(".billing-container").style.display = "none";
                    document.querySelector(".existing-order-container").style.display = "flex";
                    document.querySelector(".order-analytics-container").style.display = "none";
                } else if (j === 2) {
                    document.querySelector(".billing-container").style.display = "none";
                    document.querySelector(".existing-order-container").style.display = "none";
                    document.querySelector(".order-analytics-container").style.display = "flex";
                }
            })
        }
    }

    useEffect(() => {
        selectClick();
    }, [])

    return (
        <div className='left-component'>
            <div className='left-select active' onClick={selectClick}>
                New Order
            </div>
            <div className='left-select' onClick={selectClick}>
                Existing Orders
            </div>
            <div className='left-select' onClick={selectClick}>
                Order Analytics
            </div>
        </div>
    )
}

export default LeftComponent